.App {
  text-align: center;
  width: 40%;
  margin: 0 auto;

}

input{
  padding: 5px;
  border: 2px solid green;
}

.add-btn{
  padding: 5px ;
  width: 70px;
  margin: 5px;
  background-color: green;
  font-size: 15px;
  cursor: pointer;
  font-weight: 800;
  border: none;
}
li{
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;
  background-color: #c4c4c4;
  display: flex;
  justify-content: space-between;
  padding: 5px;
  margin-top: 5px;
}

li button{
  padding: 5px ;
  width: 70px;
  /* margin: 5px; */
  background-color: red;
  font-size: 15px;
  cursor: pointer;
  font-weight: 800;
  border: none;
}

h1{
  font-weight: bold;
}


@media  screen and (max-width:900px) {
  .App {
    text-align: center;
    width: 100%;
    margin: 0 auto;
  
  }

}